import React from 'react';
import Timer from '../timer/index';
import {RerenderTime} from 'hooks/timerWrap'

import './index.css';

// 
import {IJson} from 'types/home';

interface Props {
  data : IJson[]
  today? : number
}

const Index : React.FC<Props> = ({
  data,
}) => {
  const [setTime] = RerenderTime();

  
  //     
  const newData = [...data].map(obj => ({...obj}));
  //  ,     
  const validTimes = GetValidTimes<typeof newData>(newData);

  newData.forEach((is, index) => {
    is.time = validTimes[index];
    //  
    if(typeof is.position === 'string') return;
    //  
    //       
    if(validTimes[index].length !== 0) return is.position = (is.position as string[]).slice(-validTimes[index].length);
    //     
    return is.position = [];
  })

  //      .   ,  
  newData.sort((a, b) => {
    const 
      time_a = a.time[0],
      time_b = b.time[0];
    if(!time_b){
      return -1
    }
    if(time_a > time_b){
      return 1;
    }else if(time_a < time_b){
      return -1;
    }
    return 0;
  })

  if(newData.length === 0) return <div className="noTimerContent textCenter rem08"> </div>;
  
  return(
    <div className="timerWrap">
      {newData.map((data, index) => {
        let {name, src, time, position, endPosition, lv, endTime, waiting, contType} = data;

        if(typeof position !== 'string'){
          position = position[0] || endPosition;
        }
        return <Timer 
                  setTime={setTime} 
                  name={name}  
                  src={src}
                  time={time[0]}
                  endTime={endTime}
                  position={position}
                  lv={lv}
                  contType={contType}
                  waiting={waiting}
                  key={`timer${index}`}
                />
      })}
    </div>
  )
}

function GetValidTimes<T extends IJson[]>(newData : T){
  const date = new Date();
  return newData
  .map(is => is.time)
  .map(group => {
    return (group as string[]).filter(time => {
      const newTime = time.replace(':', '');
      const min = date.getMinutes() < 10 ? '0'+date.getMinutes() : date.getMinutes();
      const now = String(date.getHours())+String(min);
      if(Number(newTime) > Number(now)) return time;
      return null;
    })
  });
}

export default React.memo(Index, (prev, next) => {
  //    homeDate Home    , Home  today   TimeWrap  
  if(prev.today !== next.today) return false;
  return true;
});